
import { Options, Vue } from "vue-class-component";
import store from "@/store/index";
import { BaseService } from "@/services/base-service";
import { IProduct } from "@/domain/IProduct";

@Options({
    components: {},
    props: {},
})
export default class ProductsCreate extends Vue {
    id: undefined;
    companyId: string = "";
    productTypeId: string = "";
    productName: string = "";
    productSize: string = "";
    productSeason: string = "";
    productCode: string = "";

    async createClicked(event: Event): Promise<void> {
        const service = new BaseService<IProduct>(
            "https://jakaar.azurewebsites.net/api/v1/Products",
            store.state.token ? store.state.token : undefined
        );

        const objToCreate: IProduct = {
            id:
            this.id,
            companyId:
            this.companyId,
            productTypeId:
            this.productTypeId,
            productName:
            this.productName,
            productSize:
            this.productSize,
            productSeason:
            this.productSeason,
            productCode:
            this.productCode
        };

        console.log(objToCreate);

        const response = await service.post(objToCreate);

        console.log(response);
    }

    beforeCreate(): void {
        console.log("beforeCreate");
    }

    created(): void {
        console.log("created");
    }

    beforeMount(): void {
        console.log("beforeMount");
    }

    mounted(): void {
        console.log("mounted", store.state.token);
    }

    beforeUpdate(): void {
        console.log("beforeUpdate");
    }

    updated(): void {
        console.log("updated");
    }

    activated(): void {
        console.log("activated");
    }

    deactivated(): void {
        console.log("deactivated");
    }

    beforeUnmount(): void {
        console.log("beforeUnmount");
    }

    unmounted(): void {
        console.log("unmounted");
    }
}
