
import { Options, Vue } from "vue-class-component";
import store from "@/store/index";
import { BaseService } from "@/services/base-service";
import { IPrice } from "@/domain/IPrice";

@Options({
    components: {},
    props: {},
})
export default class PriceCreate extends Vue {
    id: undefined;
    productId: string = "";
    discountId: string = "";
    priceInEur: string = "";
    until: undefined

    async createClicked(event: Event): Promise<void> {
        const service = new BaseService<IPrice>(
            "https://jakaar.azurewebsites.net/api/v1/Prices",
            store.state.token ? store.state.token : undefined
        );

        const objToCreate: IPrice = {
            id:
            this.id,
            productId:
            this.productId,
            discountId:
            this.discountId,
            priceInEur:
            this.priceInEur,
            until:
            undefined
        };

        console.log(objToCreate);

        const response = await service.post(objToCreate);

        console.log(response);
    }

    beforeCreate(): void {
        console.log("beforeCreate");
    }

    created(): void {
        console.log("created");
    }

    beforeMount(): void {
        console.log("beforeMount");
    }

    mounted(): void {
        console.log("mounted", store.state.token);
    }

    beforeUpdate(): void {
        console.log("beforeUpdate");
    }

    updated(): void {
        console.log("updated");
    }

    activated(): void {
        console.log("activated");
    }

    deactivated(): void {
        console.log("deactivated");
    }

    beforeUnmount(): void {
        console.log("beforeUnmount");
    }

    unmounted(): void {
        console.log("unmounted");
    }
}
